import React, { ChangeEvent, useCallback, useEffect, useState } from 'react';
import './App.css';
import { Container, Content, UploadInput, EntriesContainer } from './styles';

import {
  UploadOutlined,
  LoadingOutlined,
  DeleteOutlined,
  ReloadOutlined
} from '@ant-design/icons'


import {
  CartesianGrid,
  Line,
  LineChart,
  BarChart,
  XAxis,
  YAxis,
  Tooltip,
  Bar,
  ResponsiveContainer
} from 'recharts';

import {
  Col,
  Button,
  message,
  Input
} from 'antd';

import api from './services/api';

interface INumberOfSalesResponse {
  number_of_sales: number;
}

interface IUploadSalesResponse {
  inserted: number;
  message: string;
}

interface IProductResponseData {
  product_code: string,
  product_description: string,
  quantity: number
}

interface ISalesResponseData {
  month: number;
  quantity: number;
  amount: number;
}

const App: React.FC = () => {
  const [lineData, setLineData] = useState<ISalesResponseData[]>([]);
  const [barData, setBarData] = useState([]);
  const [uploading, setUploading] = useState(false);
  const [numberOfSales, setNumberOfSales] = useState(0);
  const [entriesPerPage, setEntriesPerPage] = useState(20);

  const bestSellingProducts = useCallback(() =>{
    const params = {
      start_date: '2018-01-01',
      end_date: '2018-12-31',
      entries_per_page: entriesPerPage
    }

    api.get('api/v1/sales/best-selling-products', { params }).then(response => {
      const products = response.data.products.map((product: IProductResponseData) => {
        const {product_code, product_description, quantity} = product;

        return {
          "_id": `${product_code} - ${product_description}`,
          "quantity": quantity
        }
      });
      setBarData(products);
    });
  },[setBarData, entriesPerPage]);

  const salesByMonth = useCallback(() =>{
    const params = {
      start_date: '2018-01-01',
      end_date: '2018-12-31',
    }
    
    api.get('api/v1/sales/by-month', { params }).then(response => {
      setLineData(response.data.sales);
    })

  },[setLineData])

  const handleSalesChange = useCallback(async (e: ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      setUploading(true);

      const data = new FormData();

      data.append('file', e.target.files[0]);
      
      api.post<IUploadSalesResponse>('api/v1/sales/upload', data).then(response => {
        setNumberOfSales(response.data.inserted);
        setUploading(false);
        e.target.files = null;
        message.success(response.data.message);
      }).catch(err => {
        setUploading(false);
        e.target.files = null;
      });

    }
  }, [setUploading]);

  const handleReloadSalesClick = useCallback(() => {
   bestSellingProducts();
   salesByMonth(); 
  },[bestSellingProducts,salesByMonth]);

  const handleDeleteSalesClick = useCallback(() => {
    api.delete('api/v1/sales/all').then(response => {
      setBarData([])
      setLineData([])
      setNumberOfSales(0)
    }).catch(error => {
      message.error(error)
    })
  }, [setBarData, setLineData, setNumberOfSales])

  const handleEntriesInputChange = useCallback(async(e:ChangeEvent<HTMLInputElement>) =>{
    setEntriesPerPage(parseInt(e.target.value))
  }, [setEntriesPerPage])

  const CustomTooltip = ({ active, payload, label }:{active:any; payload:any; label:any}) => {
    if (active && payload && payload.length) {
      return (
        <div className="custom-tooltip">
          <p className="label">{`${label} : ${payload[0].value}`}</p>         
        </div>
      );
    }
  
    return null;
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await
          api.get<INumberOfSalesResponse>('api/v1/sales/number-of-sales');
        setNumberOfSales(response.data.number_of_sales);
      } catch (error) {
        setNumberOfSales(0);
      }
    }

    fetchData();
  }, [setNumberOfSales]);

  useEffect(()=>{

    if (numberOfSales) {
      handleReloadSalesClick();
    }

  },[numberOfSales, handleReloadSalesClick])

  
  return (
    <header className="App-header">
      <div className="App">

        <Container style={{ display: numberOfSales === 0 ? 'none' : 'flex' }}>
          <Content>
            <ResponsiveContainer minWidth={600} minHeight={300}>
              <LineChart data={lineData} >
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="month" />
                <YAxis />
                <Tooltip/>
                <Line type="monotone" dataKey="quantity" stroke="#8884d8" activeDot={{ r: 8 }} />
                <Line type="monotone" dataKey="amount" stroke="#82ca9d" />
              </LineChart>
            </ResponsiveContainer>

            <ResponsiveContainer minWidth={600} minHeight={300}>
              <BarChart data={barData}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="_id" />
                <YAxis />
                <Tooltip content={({ active, payload, label })=>CustomTooltip({active, payload, label}) } />
                <Bar name="Produtos mais vendidos" dataKey="quantity" fill="#8884d8" />
              </BarChart>
            </ResponsiveContainer>

            <EntriesContainer >
              <label htmlFor="entriesPerPage"> NÃºmero de registros: </label>
              <Input id='entriesPerPage' value={entriesPerPage} type={'number'} onChange={handleEntriesInputChange}/>
            </EntriesContainer>            

            <Button
              icon={<DeleteOutlined />}
              onClick={handleDeleteSalesClick}
              block
              ghost
            >
              Limpar banco de dados
            </Button>

            <Button
              icon={<ReloadOutlined />}
              onClick={handleReloadSalesClick}
              block
              ghost
            >
              Atualizar dados
            </Button>
          </Content>
        </Container>
        <Col style={{ display: numberOfSales !== 0 ? 'none' : 'flex' }}>
          <UploadInput>
            <label htmlFor="sales_file">
              {uploading ? <LoadingOutlined /> : <UploadOutlined />}
              <input
                type="file"
                name="sales_file"
                id="sales_file"
                onChange={handleSalesChange} />
            </label>
          </UploadInput>
        </Col>
      </div>
    </header>
  )
}


export default App;